#lead-overview-card {
	min-height: 90vh;
	max-height: 90vh;
	min-width: 95vw;
}
#lead-overview-card>.card-content {
	height: calc(100% - 40px);
	padding: 0px;
}
#lead-overview-card>.card-content>.action-row {
	border-top: 1px solid #ccc;
	align-items: center;
	padding: 20px;
}

#lead-overview-card>.card-content>#lead-form-cont {
	position: relative;
	height: 45%;
	display: grid;
	grid-template-columns: 21% 33% 42%;
	gap: 2%;
	padding: 10px;
	z-index: 300;
}
.lead-edit-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: start;
	gap: 10px;
	height: 100%;
	font-size: medium;
}
.lead-basic-info {
	height: 100%;
}
.lead-bid-chart {
	background-color: #f0f0f0;
	width: 100%;
	align-self: center;
	justify-self: center;
	border-radius: 10px;
	border: 1px solid #ccc;
	height:100%;
	overflow: hidden;
	display: grid;
	grid-template-columns: 50% 50%;
}

#lead-overview-card>.card-content>.bid-cont {
	height: 45%;
	width: 100%;
	border-top: 1px solid #ccc;
}

#lead-overview-card>.card-content>.bid-cont>.bid-table-cont {
	height: 100%;
}

#lead-overview-card>.card-content>.bid-cont>.bid-table-cont>.add-bid-row {
	position: relative;
	height: 60px;
}

#lead-overview-card>.card-content>.bid-cont>.bid-table-cont>#bid-table-header {
	height: 40px;
}

#lead-overview-card>.card-content>.bid-cont>.bid-table-cont>.bid-table {
	max-height: calc(100% - 100px);
	min-height: calc(100% - 100px);
	overflow-y:scroll;
}
.bid-summary-info {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 20px;
}
.bid-summary-info>.form-info-item {
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 0;
}
.bid-summary-info>.form-info-item>.form-info-title {
	font-size: 15px;
	font-style: italic;
}
.bid-summary-info>.form-info-item>:nth-child(2) {
	font-size: 40px;
	font-weight: 550
}
.lead-edit-form>.form-info-item {
	width: 100%;
	display: grid;
	grid-template-columns: 32.5% 62.5%;
	align-items: center;
	justify-content: center;
	gap: 5%;
}
.lead-edit-form>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 600;
}
.bid-cont {
	padding: 0px !important;
}

#lead-overview-card>.card-content>#lead-form-cont>.form-info-item {
	display: grid;
	grid-template-columns: 50% 50%;
	height: 40px;
	border-radius: 5px;
	align-items: center;
	padding-left: 10px;
	padding-right: 10px;
	border: 1px solid transparent;
}
#lead-overview-card>.card-content>#lead-form-cont>.form-info-item:hover {
	background-color: #f0f0f0;
	border: 1px solid #cfcfcf;
}

#lead-overview-card>.card-content>#lead-form-cont>.form-info-item>.form-info-title {
	width: 100%;
	font-size: larger;
	gap: 0px;
}
#lead-overview-card>.card-content>#lead-form-cont>.form-info-item>.checkbox-cont {
	justify-self: end;
}
#lead-overview-card>.card-content>#lead-form-cont>.form-info-item>:nth-child(2) {
	width: 100%;
}

/*Bid table*/
#bid-table-header {
	margin: 0px;
	padding: 0px;
	background-color: #062a49;
	color: #fff;
	align-items: center;
	justify-content: center;
	border-bottom: 1px solid #ccc;
	height: 40px;
	grid-template-columns: 10% 19% 19% 10% 19% 19% 5%;
}
#bid-table-header>div {
	background-color: transparent;
	margin: 0px;
	line-height: 40px;
	font-size: larger;
	font-weight: 600;
}
#bid-table-header>:last-child {
	border-right: 0px;
}
.bid-table>.tablerow {
	height: fit-content;
	margin: 0px;
	padding: 10px;
	border-bottom: 1px solid #ccc;
	display: grid;
	grid-template-columns: 10% 19% 19% 10% 19% 19% 5%;
	align-items: center;
	justify-content: center;
}
.bid-table>.tablerow:hover {
	background-color: #fafafa;
}
.bid-table>.tablerow>.checkbox-cont {
	align-self: center;
	justify-self: center;
	width: 25px;
}
.bid-table>.tablerow>div {
	margin: 0px;
	padding: 0px;
	align-self: center;
	justify-self: center;
}
.bid-table>.tablerow>input {
	align-self: center;
	justify-self: center;
	width: 75%;
}
.bid-table>.tablerow>.action-button {
	width: min-content;
	background-color: transparent;
	filter: unset;
}
.bid-table>.tablerow>.action-button:hover {
	filter: var(--drop-shadow);
}
.bid-tablerow>.dropdown-cont>.dropdown-list {
	z-index: 400 !important;
	height: 100px;
}
.bid-tablerow>.dropdown-cont,
.bid-tablerow>.dropdown-cont>.dropdown-option.selected {
	height: 29px !important;
}

/*Action form customization*/
.add-bid-row {
	margin: 0px;
	padding: 10px;
	border-bottom: 1px solid #ccc;
	display: grid;
	grid-template-columns: 10% 19% 19% 10% 19% 19% 5%;
	align-items: center;
	justify-content: center;
	gap: 0px;
	padding-right: 20px;
	background-color: #fcfcfc;
}

#bid-amount-form-item-cont,
#bid-progress-form-item-cont {
	width: 100%;
	display: grid;
	grid-template-columns: 0% 75% 10%;
}
#bid-amount-form-item-cont>input,
#bid-progress-form-item-cont>input {
	align-self: center;
	justify-self: center;
	width: 100% !important;
}

#bid-sold-form-item-cont {
	display: flex;
	justify-self: center;
}
#bid-sold-form-item-cont>.checkbox-cont {
	height: 25px;
	width: 25px;
}
#bid-cat-form-item-cont>.dropdown-cont>.dropdown-list {
	height: 170px;
}

#bid-cat-form-item-cont,
#bid-dept-form-item-cont {
	width: 60%;
	align-self: center;
	justify-self: center;
}

.add-bid-row>.action-button {
	align-self: center;
	justify-self: center;
}

/*Editor form customization*/
#editor-project-name>div {
	font-size: 2.75rem;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* number of lines to show */
			line-clamp: 1; 
	-webkit-box-orient: vertical;
}
.editor-stage {
	font-size: 20px;
}
.editor-stage>.form-info-title {
	font-weight: 600;
}
.editor-stage>:nth-child(2) {
	text-transform: capitalize;
}
#editor-status {
	text-align: left;
	font-size: x-large;
	text-transform: capitalize;
	font-style: italic;
}

#editor-name {
	font-size: 27.5px;
	font-weight: 550;
	color: #313131;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* number of lines to show */
			line-clamp: 1; 
	-webkit-box-orient: vertical;
}

#editor-contact-info {
	text-align: left;
	font-size: large;
}

#editor-cityzip, #editor-address {
	font-style: italic;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#lead-form-cont {
	}
	.lead-edit-form {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: start;
		gap: 10px;
		height: 100%;
	}
	.lead-edit-form>.form-info-item>.form-info-title {
		font-size: small;
	}
	.lead-edit-form>.form-info-item>.dropdown-cont,
	.lead-edit-form>.form-info-item>.dropdown-cont>.dropdown-option.selected {
		height: 20px;
		line-height: 10px !important;
		border-radius: 2.5px !important;
	}
	.lead-edit-form>.form-info-item>.checkbox-cont {
		height: 17.5px;
		width: 17.5px;
	}
	#editor-status {
		font-size: smaller;
	}
	.editor-stage {
		font-size: 15px;
	}
	#editor-contact-info {
		font-size: 12px;
	}
	#editor-name {
		font-size: 12px;
	}
	#editor-project-name>div {
		font-size: 2.25rem;
		overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 1; /* number of lines to show */
				line-clamp: 1; 
		-webkit-box-orient: vertical;
	}

	#lead-overview-card>.card-content>.bid-cont>.bid-table-cont>.add-bid-row {
		position: relative;
		height: 40px;
	}
	.add-bid-row {
		align-items: start;
		justify-content: start;
	}
	.add-bid-row>.form-info-item>input {
		height: 20px;
	}
	.add-bid-row>.form-info-item>.checkbox-cont {
		height: 20px !important;
		width: 20px !important;
	}
	.add-bid-row>.action-button {
		height: 20px;
		width: 20px;
		line-height: 20px;
	}
	.add-bid-row>.action-button>img {
		height: 15px;
		width: 15px;
	}
	.add-bid-row>.form-info-item>.dropdown-cont,
	.add-bid-row>.form-info-item>.dropdown-cont>.dropdown-option.selected {
		height: 20px !important;
		border-radius: 5px;
		line-height: 10px;
	}

	.bid-tablerow>.dropdown-cont, 
	.bid-tablerow>.dropdown-cont>.dropdown-option.selected,
	.bid-tablerow>input {
		height: 20px !important;
		line-height: 10px;
	}
	.bid-tablerow>.checkbox-cont {
		height: 20px !important;
		width: 20px !important;
	}
	.bid-tablerow>.action-button,
	.bid-tablerow>.action-button>img {
		height: 20px;
		width: 20px;
	}
	#bid-table-header {
		height: 30px !important;
	}
	#bid-table-header>div {
		line-height: 30px;
	}
	#bid-cat-form-item-cont,
	#bid-dept-form-item-cont {
		width: 100%;
		align-self: center;
		justify-self: center;
		height: 30px;
	}
	.bid-summary-info {
		justify-content: start;
	}

	.bid-summary-info>.form-info-item>.form-info-title {
		font-size: 12px;
	}
	.bid-summary-info>.form-info-item>:nth-child(2) {
		font-size: 25px;
	}

	.lead-bid-chart {
		grid-template-columns: 35% 65%;
	}
	#lead-overview-card>.card-content>.action-row {
		padding: 5px;
	}
}