#analytics-card {
	height: calc(100vh - 80px);
	min-height: calc(100vh - 40px);
	max-height: calc(100vh - 40px);
	min-width: calc(100% - 20px);
	max-width: calc(100% - 20px);
	overflow: hidden;
}
#analytics-card>.card-menubar {
	position: relative;
}
.medium-cards>#analytics-card {
	min-width: 100%;
	max-width: 100%;
}

#analytics-card>.card-content>.menu-bar {
	display: flex;
	border-bottom: 1px solid #ccc;
}

#analytics-card>.card-content>.menu-bar>.menu-button {
	background-color: #103b61;
}
#analytics-card>.card-content>.menu-bar>.menu-button:hover,
#analytics-card>.card-content>.menu-bar>.menu-button.selected {
	background-color: #062a49;
}

#analytics-card-card-content {
	height: calc(100% - 110px);
}
#analytics-card-card-content>.card-content {
	padding: 0px;
	min-height: 100%;
	max-height: 100%;
	height: 100%;
	overflow-y: auto;
}
/*Table header colors*/
#analytics-card-card-content>.tablerow.header,
#optionsTable>.tablerow.header,
#monthly-table-cont>.tablerow.header {
	background-color: #f0f0f0;
}
/*Table row grid*/
#analytics-card-card-content>.tablerow.header,
#analytics-card-card-content>.card-content>.tablerow,
#optionsTable>.tablerow.header,
#optionsTable>.card-content>.tablerow,
#monthly-table-cont>.tablerow.header,
#monthly-table-cont>.card-content>.tablerow {
	grid-template-columns: repeat(6, 16%);
	align-items: center;
	justify-content: center;
	padding: 0px;
	line-height: 40px;
	min-height: 40px;
	height: fit-content;
	border-bottom: 1px solid #ccc;
}
/*Border bottom on table row*/
#analytics-card-card-content>.card-content>.tablerow,
#optionsTable>.card-content>.tablerow,
#monthly-table-cont>.card-content>.tablerow {
	border-bottom: 1px solid #e1e1e1;
}
/*Table row Headers & Table Rows*/
#tracker-month-view-card-content>.tablerow.header,
#optionsTable>.tablerow.header,
#monthly-table-cont>.tablerow.header,
#analytics-card-card-content>.tablerow.header {
	background-color: #f0f0f0;
	color: #212121;
	border-bottom: 1px solid #ccc;
	font-weight: 600;
	line-height: 40px;
	display: none;
}
#optionsTable>.tablerow.header {
	font-size: larger;
}
#tracker-month-view-card-content>.tablerow.header.show,
#optionsTable>.tablerow.header.show,
#monthly-table-cont>.tablerow.header.show,
#analytics-card-card-content>.tablerow.header.show {
	display: grid;
}
/*Reset padding/margin/bg color*/
#analytics-card-card-content>.tablerow.header>div,
#optionsTable>.tablerow.header>div,
#optionsTable>.card-content>.tablerow>div,
#analytics-card-card-content>.card-content>.tablerow>div,
#monthly-table-cont>.tablerow.header>div,
#monthly-table-cont>.card-content>.tablerow>div {
	background-color: transparent;
	margin: 0px;
	padding: 0px;
	color: #212121;
	filter: unset;
	line-height: 40px;
}
/*Table row hover*/
#analytics-card-card-content>.card-content>.tablerow:hover,
#optionsTable>.card-content>.tablerow:hover,
#monthly-table-cont>.card-content>.tablerow:hover {
	background-color: #fafafa;
}
/*Header borders*/
#analytics-card-card-content>.tablerow.header>div,
#optionsTable>.tablerow.header>div
#monthly-table-cont>.tablerow.header>div {
	font-weight: 600;
	font-size: larger;
}
#analytics-card-card-content>.tablerow.header>:last-child,
#optionsTable>.tablerow.header>:last-child,
#monthly-table-cont>.tablerow.header>:last-child {
	border-right: 0px;
}
/*Adjust table grid*/
#tracker-month-view-card-content>.tablerow.header,
#tracker-month-view-card-content>.data-table>.tablerow {
	grid-template-columns: repeat(6, 16%);
}

/*Summary View*/
.summary-page-container {
	height: calc(100%);
}
#optionsTable {
	width: 100%;
	overflow: hidden !important;
	min-height: 100%;
	max-height: 100%;
}
#optionsTable>.card-content {
	height: calc(100% - 25px);
	padding: 0px;
	overflow-y: auto;
}
#optionsTable>.card-content>:last-child {
	border-bottom: 0px;
}
/*Summary view table grid*/
#optionsTable>.card-content>.tablerow,
#optionsTable>.tablerow.header {
	grid-template-columns: repeat(6, 16%);
	height: 35px;
}

.summary-form-cont {
	padding: 1rem;
	display: flex;
	flex-direction: column;
	width: 100%;
	height: min-content;
	row-gap: 1.25em;
}
.summary-form-cont>.form-info-item {
	display: grid;
	grid-template-columns: 70% 20%;
	gap: 5%;
	font-size: large;
	align-items: center;
	justify-content: center;
	padding: 0px;
}
.summary-form-cont>.form-info-item>:nth-child(2) {
	font-weight: 600;
	text-align: left;
}
.summary-form-cont>.form-info-item>.form-info-title {
	font-style: italic;
	width: 100%;
}
.summary-form-cont>.form-info-item>.form-info-title::after {
	content: ':';
}
#closingperc-cont>:nth-child(2)::after,
.row-item.freq::after {
	content: '%';
}
#options-table>.tablerow-container>.table-row>:nth-child(5)::after {
	content: '%';
}
#analytics-card-card-content>.tablerow-container>.table-row>:nth-child(4)::after {
	content: '%';
}

.row-item.avgrev::before {
	content: '$';
}

.tablerow.header>.row-item.avgrev::before,
.tablerow.header>.row-item.freq::after {
	content: unset;
}

/*Monthly View*/
#monthly-table-cont>.card-content>.tablerow,
#monthly-table-cont>.tablerow.header {
	grid-template-columns: repeat(16, 6.25%);
	font-size: small;
}
#monthly-table-cont {
	height: calc(100% - 20px);
}
#monthly-table-cont>.card-content {
	padding: 0px;
	height: 100%;
	overflow-y: scroll;
}
#monthly-table-cont>.card-content>:last-child {
	border-bottom: 0px;
}

.summary-chart-cont {
	display: flex;
	flex-direction: column;
	gap: 5%;
	padding: 10px;
	height: calc(100% + 20px);
}

.sum-charts-cont {
	height: 60%;
	width: 100%;
	display: grid;
	grid-template-columns: 60% 40%;
}

.sum-charts-cont>.data-chart {
	height: 100%;
	width: 100%;
	background-color: #fafafa;
	padding: 20px;
	border-radius: 10px;
	border: 1px solid #ccc;
}

.pie-chart {
	height: 35%;
	width: 100%;
	background-color: #fafafa;
	padding: 5px;
	border-radius: 10px;
	border: 1px solid #ccc;
	display: grid;
	grid-template-columns: 36.5% 62.5%;
	gap: 2.5%;
}
.pie-chart>.pie-chart-form {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: center;
	gap: 10%;
}
.form-top {
}
.form-flex {
	display: flex;
	gap: 5%;
	align-items: center;
}
.form-top>.form-info-item,
.form-flex>.form-info-item {
	display: flex;
	flex-direction: column;
}
.form-top>.form-info-item>.form-info-title,
.form-flex>.form-info-item>.form-info-title {
	font-size: 20px;
	font-style: italic;
}
.form-top>.form-info-item>:nth-child(2),
.form-flex>.form-info-item>:nth-child(2) {
	font-size: 40px;
	font-weight: 550
}
.form-flex>.form-info-item>.form-info-title {
	font-size: 17.5px;
	font-style: italic;
}
.form-flex>.form-info-item>:nth-child(2) {
	font-size: 30px;
	font-weight: 550;
	color: #303030;
}

.month-header {
	height: 10%;
}
.month-header>div {
	font-size: 20px;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#analytics-card-card-content>.menu-bar>.menu-button {
		height: 35px;
		line-height: 35px;
	}
	.month-header {
		height: 10%;
	}
	.month-header>div {
		font-size: 15px;
	}
	.form-flex>.form-info-item>.form-info-title {
		font-size: 17.5px;
		font-style: italic;
	}
	.form-top>.form-info-item>.form-info-title, .form-flex>.form-info-item>.form-info-title {
		font-size: 12.5px;
	}
	.form-top>.form-info-item>:nth-child(2), .form-flex>.form-info-item>:nth-child(2) {
		font-size: 20px;
	}
	.summary-form-cont {
		row-gap: 10px;
	}
	.summary-form-cont>.form-info-item {
		font-size: 15px;
		padding: 0px;
		grid-template-columns: 60% 30% !important;
	}

	.pie-chart>.pie-chart-form {
		padding: 0px;
	}

	.pie-chart {
		height: 35%;
		width: 100%;
		background-color: #fafafa;
		padding: 5px;
		border-radius: 10px;
		border: 1px solid #ccc;
		display: grid;
		grid-template-columns: 36.5% 62.5%;
		gap: 2.5%;
		overflow: hidden;
	}
	.pie-chart>.recharts-responsive-container {
		height: 100% !important;
		width: 100% !important;
		font-size: xx-small;
	}
	.pie-chart>.recharts-responsive-container>.recharts-wrapper {
		width: 200px !important;
		height: 100px !important;
	}
	.pie-chart>.recharts-responsive-container>.recharts-wrapper>.recharts-legend-wrapper {

	}
	.summary-chart-cont {
		height: 100% !important;
	}
	#analytics-card-card-content {
		height: calc(100% - 75px);
	}
}