body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.no-padding {
	padding: 0px;
}

.capitalize {
	text-transform: capitalize;
}

.side-bar-menu-cont>.side-bar-menu-controls>:first-child>img {
	transform: rotate(180deg);
	transition: 0.5s;
}
.side-bar-menu-cont.collapsed>.side-bar-menu-controls>:first-child>img {
	transform: unset;
}

/*HEADERS*/
.table-row.header {
	background-color: transparent;
	margin: 0px;
	padding: 0px;
	border-bottom: 1px solid #ccc;
	background-color: #efefef;
}
.table-row.header:hover {
	background-color: #efefef;
}
.table-row.header>.rowitem {
	background-color: transparent;
	margin: 0px;
	border-right: 1px solid #ccc;
	padding: 10px;
	font-weight: 600;
}

/*ROW ITEM*/
.rowitem {
	margin: 0px;
	background-color: transparent;
}
.rowitem.clickable {
	cursor: pointer;
	background-color: aqua;
}
.rowitem.clickable:hover {
	background-color: #ccc;
}

/*TABLEROW*/
.table-row {
	height: fit-content;
	padding: 10px;
	border-bottom: 1px solid #ccc;
	display: grid;
	grid-template-columns: repeat(6, 16%)
}
.table-row.clickable {
	cursor: pointer;
}
.table-row:hover {
	background-color: #fafafa;
}

/*CONTAINER*/
.tablerow-container {
	height: calc(100% - 40px);
	overflow-y: auto;
}

/*Global Filters*/
.global-filter-card {
	min-height: unset;
	height: fit-content;
	max-height: 70vh;
	width: 30vw;
}

.global-filter-card>.card-content {
	height: calc(100% - 40px);
	overflow-y: scroll;
}

.filter-card-body {
	height: 90%;
}

.filter-card-footer {
	height: 10%;
	display: flex;
	align-items:flex-end;
}