#tracker-search-card {
	min-width: 80vw !important;
	height: 70vh;
}

#tracker-search-card-card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
}
#tracker-search-card-table {
	height: 90%;
	padding: 0px;
	border-radius: 10px;
	overflow: hidden;
	border: 1px solid #ccc;
}

#tracker-search-card-table>.card-content.data-table {
	height: calc(100% - 0px);
}
#tracker-search-card-table>.tablerow.header {
	background-color: #f0f0f0;
	border-bottom: 1px solid #ccc;
	padding: 0px;
}
#tracker-search-card-table>.tablerow.header>div {
	border-right: 1px solid #ccc;
	line-height: 40px;
	font-weight: 600;
}
#tracker-search-card-table>.tablerow.header>:last-child {
	border-right: 0px;
}
#tracker-search-card-table>.tablerow.header>div,
#tracker-search-card-table>.card-content.data-table>.tablerow>div {
	background-color: transparent;
	margin: 0;
}

#tracker-search-card-table>.card-content.data-table>.tablerow {
	height: fit-content;
	cursor: pointer;
	border-bottom: 1px solid #ccc;
}
#tracker-search-card-table>.card-content.data-table {
	padding: 0px;
}

#tracker-search-card-table>.card-content.data-table>.tablerow:hover {
	background-color: #f3f3f3;
}

#tracker-search-actions {
	display: grid;
	grid-template-columns: 42% 14.5% 14.5% 14.5% 14.5%;
	gap: 0px;
	margin: 5px;
	height: 10%;
	overflow: hidden;
}
#tracker-search-actions>.form-info-item {
	display: grid;
	grid-template-columns: 70% 20%;
	gap: 5%;
}
#tracker-search-actions>:first-child {
	display: grid;
	grid-template-columns: 22.5% 72.5%;
	gap: 5%;
}
#tracker-search-actions>.form-info-item>.form-info-title {
	width: 100%;
	font-size: larger;
	font-weight: 600;
}
#tracker-search-actions>.form-info-item>input {
	width: 100%;
}
#tracker-search-actions>.form-info-item>.checkbox-cont {
	height: 20px;
	width: 20px;
}