#tracker-view-cont {
	height: calc(100vh - 80px);
	min-height: calc(100vh - 80px);
	max-height: calc(100vh - 80px);
	width: 100%;
	display: flex;
	flex-direction: column-reverse;
	justify-content: start;
	gap: 1%;
	overflow: hidden;
}

#lead-summary-card {
	min-width: 99.5%;
	min-height: 30%;
	max-height: 30%;
	background-color: transparent;
	border: 0px;
}
#lead-summary-card>.card-menubar {
	position: relative;
	display: none;
}
#lead-summary-card>.card-content {
	padding: 0px;
}
#tracker-month-view {
	min-width: 99.5%;
	min-height: 69%;
	max-height: 69%;
}

#lead-summary-card>.card-content {
	display: grid;
	grid-template-columns: 49.5% 49.5%;
	gap: 1%;
	height: calc(100% - 40px);
}

.lead-info-card {
	min-height: 100%;
	max-height: 100%;
	width: 100%;
	height: 100% !important;
}