.lead-summary-form {
	display: grid;
	grid-template-columns: 49% 49%;
	gap: 1%;
	height: 30%;
}
.lead-info-card>.card-menubar {
	position: relative;
}
.lead-info-card>.card-content {
	display: grid;
	grid-template-columns: 50% 50%;
	align-items: start;
	justify-content: start;
	height: calc(100% - 40px);
	padding: 5px;
}
.lead-sum-form-item {
	width: 95%;
	display: grid;
	grid-template-columns: 45% 55%;
	align-items: center;
	justify-content: center;
	gap: 0px;
	font-size: 1.1rem;
	border: 1px solid transparent;
}
.lead-sum-form-item>.form-info-title {
	font-weight: 600;
}
.lead-sum-form-item>div {
	font-style: italic;
	color: #333333;
	text-align: end;
}
.lead-sum-form-item#closepercent-cont>div::after {
	content: "%";
}
/*Adjust lead summary form when viewed in medium cards*/
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card>.card-content>.lead-sum-form-item {
	font-size: large;
}
.medium-cards>#tracker-view-cont>#lead-summary-card>.card-content>.card>.card-content {
	display: grid;
	grid-template-columns: 50% 50%;
}